              <span class="zd-common-fl zd-common-mr14">部门审核人：<span class="zd-common-font-weight">{{ allDetail.deptCheckName }}</span></span>
              <span class="zd-common-fl zd-common-mr14">领用类型：<span class="zd-common-font-weight">{{ allDetail.kindName }}</span></span>
            </div>
            <Table class="zd-common-overflow-v zd-common-mt10" border :columns="goodsCols" :data="goodsData" show-summary></Table>
            <div class="zd-common-mt10 zd-common-clearFix">
              <span class="zd-common-clearFix"><span class="zd-common-fl">申领原因：</span><span class="zd-common-fl zd-cm-applyReason">{{ allDetail.reason }}</span></span>
            </div>
            <div class="zd-common-mt10 zd-common-clearFix">
              <span class="zd-common-clearFix"><span class="zd-common-fl">部门审核状态：</span><span class="zd-common-fl zd-cm-remark">{{ allDetail.statusName }}</span></span>
            </div>
            <div class="zd-common-mt10 zd-common-clearFix">
              <span class="zd-common-clearFix"><span class="zd-common-fl">部门审核意见：</span><span class="zd-common-fl zd-cm-remark">{{ allDetail.deptOpinion }}</span></span>
