</template>
<script>
  import { utils } from '@comm/script/utils/index'
  export default {
    data() {
      return {
        //列表
        columns: [
          {
            type: 'index',
            align: 'center',
            title: '序号',
            width:65
          },
          {
            title: '申领单号',
            align: 'center',
            key: 'applyNum'
          },
          {
            title: '申领部门',
            align: 'center',
            key: 'applyDepart',
          },
          {
            title: '申领日期',
            align: 'center',
            key: 'applyDate',
          },
          {
            title: '申领类型',
            align: 'center',
            key: 'applyType',
          },
          {
            title: '申领数量',
            align: 'center',
            key: 'goodsNum',
          },
          {
            title: '申请人',
            align: 'center',
            key: 'applyPerson',
          },
          {
            title: '当前状态',
            align: 'center',
            key: 'status',
          },
          {
            title: '操作',
            align: 'center',
            key: 'action',
            width:180,
            render: (h, params) => {
              if(params.row.canCheck){
                return h('div', [
                  h('Button', {
                    props:{
                      type:'primary',
                      ghost:true,
                    },
                    style: {
                      height: "28px",
                      padding:'0 15px 1px',
                      marginRight:'10px'
                    },
                    on: {
                      click: (e) => {
                        e.stopPropagation();
                        this.showCheckModal(params.row.id);
                      }
                    }
                  }, '审核'),
                  h('Button', {
                    props:{
                      type:'primary',
                      ghost:true,
                    },
                    style: {
                      height: "28px",
                      padding:'0 15px 1px'
                    },
                    on: {
                      click: (e) => {
                        e.stopPropagation();
                        this.showViewModal(params.row.id);
                      }
                    }
                  }, '查看'),
                ]);
              }else{
                return h('div', [
                  h('Button', {
                    props:{
                      type:'primary',
                      ghost:true,
                    },
                    style: {
                      height: "28px",
                      padding:'0 15px 1px'
                    },
                    on: {
                      click: (e) => {
                        e.stopPropagation();
                        this.showViewModal(params.row.id);
                      }
                    }
                  }, '查看'),
                ]);
              }
            }
          }
        ],
        data: [],
        pageSize:10,
        pageNum:1,
        total:0,

        //批量审核
        tabArr:[],
        selectAllArr:[],
        batchCheckModal:false,
        active:0,
        activeId:'',
        checkForm:{
          status:'',
          checkReason:''
        },
        msgList:[],

        //申领单审核
        checkModal:false,
        checkSingleForm:{
          status:'',
          checkReason:''
        },
        ruleValidate: {
          status: [
            { required: true, message:'不能为空', trigger: 'change' }
          ],
        },
        saveLoading:false,
        checkId:'',

        //申领单查看
        viewModal:false,
        goodsCols:[
          {
            type: 'index',
            title:'序号',
            align: 'center',
            width:65
          },
          {
            title: '物品名称',
            align: 'center',
            key: 'name',
          },
          {
            title: '物品编码',
            align: 'center',
            key: 'code',
          },
          {
            title: '物品分类',
            align: 'center',
            key: 'classify',
          },
          {
            title: '品牌',
            align: 'center',
            key: 'brand',
          },
          {
            title: '规格/型号',
            align: 'center',
            key: 'model',
          },
          {
            title: '单位',
            align: 'center',
            key: 'unit',
          },
          {
            title: '数量',
            align: 'center',
            key: 'number',
          }
        ],
        goodsData:[],
        allDetail:{},

        //查询
        startDate:'',
        endDate:'',
        departArr:[],
        department:'',
        typeArr:[],
        type:'',
        statusArr:[],
        status:'',
        orderNO:'',
        dateOptions: this.dateRange,
        selectLoading:false,
      }
    },
    created(){
      this.getApplyKindList();
      this.getCheckUserByDepartmentId();
      this.getDeptCheckStatus();
      this.getDeptCheckPage(this.pageNum,this.pageSize,this.startDate,this.endDate,this.department,this.type,this.status,this.orderNO);
    },
    methods: {
      //日期
      changeDate(arr){
        this.startDate = arr[0];
        this.endDate = arr[1];
      },
      //获取状态
      getDeptCheckStatus() {
        BaseAxios({
          method: 'post',
          url: '/sr/storeroomData!getDeptCheckStatus.action',
          data:{},
        })
          .then((response) => {
            let aData = response.data;
            this.statusArr = Object.entries(aData);
          })
          .catch((error) => {
            this.postErrorTip('请求失败');
            console.log(error);
          });
      },
      //批量审核
      batchCheckHandle(){
        let sArr = this.selectAllArr;
        if(sArr.length>0){
          this.batchCheckModal=true;
          this.active = 0;
          this.activeId = this.tabArr[0].id;
          this.checkForm.status = this.tabArr[0].checkStatus;
          this.checkForm.checkReason = this.tabArr[0].checkReason;
        }else{
          this.postWarningTip("请先选择申领单");
        }
      },
      clickRowHandle(row,index){
        if(row.canCheck){
          if(this.selectAllArr.indexOf(row.id)>-1){
            let index = this.selectAllArr.indexOf(row.id);
            this.selectAllArr.splice(index,1);
            this.tabArr.splice(index,1);
          }else{
            if(this.selectAllArr.length>=10){
              return this.postWarningTip('最多选择10条');
            }else{
              this.$set(row,'checkStatus','');
              this.$set(row,'checkReason','');
              this.selectAllArr.push(row.id);
              this.tabArr.push(row);
            }
          }
        }else{
          this.postWarningTip('已经审核');
        }
      },
      rowClassName(row, index) {
        if (this.selectAllArr.indexOf(row.id)>-1) {
          return 'demo-table-info-row';
        }
        return '';
      },
      changeLiHandle(index,id){
        this.activeId = id;
        this.active = index;
        this.checkForm.status = this.tabArr[index].checkStatus;
        this.checkForm.checkReason = this.tabArr[index].checkReason;
        this.getDeptCheckDetail(id);
      },
      clearHandle(id,index){
        this.tabArr.map((item,i)=>{
          if(item.id === id){
            this.tabArr.splice(i,1);
          }
        });
        let curIndex = 0;
        if(index===0){
          curIndex = 0;
        }else{
          curIndex = index - 1;
        }
        this.active = curIndex;
        this.activeId = this.tabArr[curIndex].id;
        this.selectAllArr.splice(this.selectAllArr.indexOf(id),1);
      },
      changeCheckStatus(val){
        this.tabArr[this.active].checkStatus = val;
      },
      changeCheckReason(){
        this.tabArr[this.active].checkReason = this.checkForm.checkReason;
      },
      cancelBatchCheck(){
        this.batchCheckModal = false;
        this.selectAllArr = [];
        this.tabArr = [];
      },
      //单个审核
      showCheckModal(id){
        this.checkModal = true;
        this.checkId = id;
        this.getDeptCheckDetail(id);
        this.$refs['checkSingleForm'].resetFields();
        this.checkSingleForm.checkReason = "";
      },
      cancelCheck(){
        this.checkModal = false;
        this.checkSingleForm.checkReason = "";
      },
      //申领单审核保存验证
      saveDeptCheck(type,arr) {
        const params = {};
        if(type==='single'){
          params['recordCheckVOs[0].recordId'] = this.checkId;
          params['recordCheckVOs[0].status'] = this.checkSingleForm.status;
          params['recordCheckVOs[0].opinions'] = this.checkSingleForm.checkReason;
        }else{
          arr.map((item,index)=>{
            params['recordCheckVOs['+index+'].recordId'] = item.id;
            params['recordCheckVOs['+index+'].status'] = item.checkStatus;
            params['recordCheckVOs['+index+'].opinions'] = item.checkReason;
          })
        }

        BaseAxios({
          method: 'post',
          url: '/sr/storeroomData!saveDeptCheck.action',
          data:params,
        })
          .then((response) => {
            this.saveLoading=false;
            this.batchCheckModal=false;
            this.checkModal = false;
            this.selectAllArr = [];
            this.tabArr = [];
            this.getDeptCheckPage(this.pageNum,this.pageSize,this.startDate,this.endDate,this.department,this.type,this.status,this.orderNO);
            this.$store.dispatch('getDeptCheckNum');
          })
          .catch((error) => {
            this.saveLoading=false;
            this.batchCheckModal=false;
            this.checkModal = false;
            this.postErrorTip('请求失败');
            console.log(error);
          });
      },
      handleSubmit(name) {
        let tmp = [];
        this.$refs[name].validate((valid) => {
          if (valid) {
            if(this.tabArr.length>0){
              this.tabArr.map((item,index)=>{
                if(item.checkStatus === ""){
                  tmp.push({
                    detail:item,
                    index:index
                  });
                }
              })
              if(tmp.length>0){
                return this.postWarningTip("第"+tmp[0].index+"个"+tmp[0].detail.applyPerson+"的申领单还未审核");
              }else{
                this.saveLoading=true;
                this.saveDeptCheck('multiple',this.tabArr);
              }
            }else{
              this.saveLoading=true;
              this.saveDeptCheck('single');
            }
          }
        })
      },
      //申领单查看
      showViewModal(id){
        this.viewModal = true;
        this.getDeptCheckDetail(id);
      },
      getDeptCheckDetail(ids) {
        this.goodsData = [];
        BaseAxios({
          method: 'post',
          url: '/sr/storeroomData!getCheckList.action',
          data:{
            ids:ids
          },
        })
          .then((response) => {
            let aData = response.data[0];
            this.allDetail = aData;
            this.msgList = aData.msgList;
            aData.goodsList.map((item)=>{
              this.goodsData.push(
                {
                  name:item.name,
                  code:item.code,
                  classify:item.goodsKind,
                  brand:item.brand,
                  model:item.size,
                  unit:item.unit,
                  number:item.count
                }
              )
            })
          })
          .catch((error) => {
            this.postErrorTip('请求失败');
            console.log(error);
          });
      },
      //列表
      getDeptCheckPage(pageNum,pageSize,startDate,endDate,departmentId,kind,status,num) {
        this.data = [];
        BaseAxios({
          method: 'post',
          url: '/sr/storeroomData!getDeptCheckPage.action',
          data:{
            pageNum:pageNum,
            pageSize:pageSize,
            startDate:startDate,
            endDate:endDate,
            departmentId:departmentId,
            kind:kind,
            status:status,
            num:num,
          },
        })
          .then((response) => {
            let aData = response.data;
            if(aData && aData.list.length>0){
              aData.list.map((item)=>{
                this.data.push({
                  id:item.id,
                  applyNum:item.num,
                  applyDepart:item.department,
                  applyDate:item.date,
                  applyType:item.kindName,
                  goodsNum:item.goodsNum,
                  applyPerson:item.userName,
                  status:item.statusName,
                  canCheck:item.canCheck,
                })
              })
            }
            this.total = aData.totalCount;
            this.selectLoading = false;
          })
          .catch((error) => {
            this.selectLoading = false;
            this.postErrorTip('请求失败');
            console.log(error);
          });
      },
      //分页
      changePage(num) {
        this.pageNum = num;
        this.getDeptCheckPage(this.pageNum,this.pageSize,this.startDate,this.endDate,this.department,this.type,this.status,this.orderNO);
      },
      changePageSize(num) {
        this.pageSize = num;
        this.getDeptCheckPage(this.pageNum,this.pageSize,this.startDate,this.endDate,this.department,this.type,this.status,this.orderNO);
      },
      //查询
      selectHandle(){
        this.selectLoading = true;
        this.getDeptCheckPage(this.pageNum,this.pageSize,this.startDate,this.endDate,this.department,this.type,this.status,this.orderNO);
      },
    },
    mounted() {
      this.calcWidth();
    }
  }
</script>
<style lang="less">
  /*适配*/
  @media screen and (max-width: 1471px){
    .zd-cm-res-fr{
      float: left !important;
    }
    .zd-cm-res-fr > *,.zd-cm-res-fl{
      margin-bottom:10px;
    }
  }
</style>



